<template>
  <div class="searchContainer">
    <a-input-search placeholder="Search Any Image" style="width: 200px" @search="onSearch" :loading="search.loading" enter-button />
    <div class="searchResultText">
      <b>Your Input</b>: {{ search.keywordMatched }} 
      <b></b>
      <b>Our Suggestion</b>: {{ search.suggestionGoods }}
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import { mapMutations } from 'vuex'
import result from '@/assets/result'

export default {
  data () {
    return {
      search: {
        loading: false,
        keywordMatched: '?',
        suggestionGoods: '?'
      },
      cse: {
        link: 'https://customsearch.googleapis.com/customsearch/v1',
        payload: {
          key: 'AIzaSyC0v-ouowp4PBrBrySs2WdgrOAQ51usCfg',
          q: '',
          cx: '011886738805883185466:ltw2aktxzse',
          searchType: 'image',
          num: 1
        }
      },
      image: {
        input: '',
        output: ''
      },
      aiResult: result
    }
  },

  methods: {
    ...mapMutations(['updateInputImage', 'updateOutputImage']),

    onSearch (value) {
      // let searchLink = 'https://api.unsplash.com/search/photos?client_id=ANj5PKrpuatH3x-AHg-J2bg5JFK8IOgQ5HFVdYF7maI&query=' + value
      let { payload } = this.cse
      let queryParams = this.queryParams;
      
      this.search.loading = true
      
      let k = value.toLowerCase()
      let res = []

      Object.entries(this.aiResult).forEach((el, ind) => {
        if (el[0].toLowerCase().includes(k)) {
          // console.log('matched ', el[0])
          // console.log(el[1])
          let temp = {}
          temp.input = el[0]
          temp.output = el[1].TOP1
          res.push(temp)
        }
      })

      if (res && res[0]) {
        this.search.keywordMatched = res[0].input
        this.search.suggestionGoods = res[0].output

        axios
          .get(
            this.cse.link + '?' + queryParams(
              {
                ...payload,
                q: this.search.keywordMatched.replace(' ', '+')
              }
            )
          )
          .then(({ data }) => {
            let { items } = data
            this.updateInputImage(items[0].link)
          })

        axios
          .get(
            this.cse.link + '?' + queryParams(
              {
                ...payload,
                q: this.search.suggestionGoods
              }
            )
          )
          .then(({ data }) => {
            let { items } = data
            this.updateOutputImage(items[0].link)
            this.search.loading = false
          })
      }      
    },

    queryParams (source) {
      let array = [];

        for(let key in source) {
          array.push(encodeURIComponent(key) + "=" + encodeURIComponent(source[key]));
        }

        return array.join("&");
    }
  },

  created () {
    
  }
}
</script>

<style>
  .searchContainer {
    max-width: inherit;
    margin: 20px;
  }

  .searchResultText {
    margin-top: 15px
  }
</style>



